.loader-component{
    font-family: 'Oswald';
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    z-index: 1;
    color: rgb(172, 172, 172);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    font-size: 8rem;
    color: transparent;
    -webkit-text-stroke: 10px rgb(212, 212, 212);
    letter-spacing: -0.5rem;
}
@media (max-width: 480px) {
    .loader-component{
        font-size: 1rem;
        -webkit-text-stroke: 0px rgb(212, 212, 212);
        color: white;
        letter-spacing: 0rem;
        background-color: black;
    }
}